import React, {Component } from "react";
//import { makeStyles } from "@material-ui/core/styles";
import { withStyles,Theme } from "@material-ui/core/styles";
import { WithStyles, createStyles } from "@material-ui/core";
import { SkillPack } from "./MsInterfaces";
import Box from "@material-ui/core/Box";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import Select from "@material-ui/core/Select";

const selectorsStyles = (theme:Theme) => createStyles({
	ccSelect: {
	  minWidth: "4rem",
	  width: "9rem"
	}
 });

 //All this for TypeScript to be able to use classes prop injected by
 //withStyles since we can't use the useStyles hook in class components. 
 //Things are much easier using functional components. Using class
 //component here mainly as a learning exercise.
interface SelectorsProps extends WithStyles<typeof selectorsStyles> {
	cultures:SkillPack[],
	careers:SkillPack[],
	onChange?(culture:number, career:number):void
}

interface SelectorsState {
	aCulture?:number,
	aCareer?:number
}

class Selectors extends Component<SelectorsProps,SelectorsState> {

	constructor (props:SelectorsProps) {
		super(props);
		this.state={ aCulture: 0, aCareer: 0 };
	}
	
	//Material-ui select has a custom onChange callback
	handleChange = (e:any) => {
		this.setState({[e.target.name]: e.target.value},()=>{
			if (this.props.onChange===undefined) return;
			this.props.onChange(
				this.state.aCulture === undefined ? -1 : this.state.aCulture,
				this.state.aCareer === undefined ? -1 : this.state.aCareer
			);
		});
	}

	render() {
		const { classes } = this.props;
		const cultureMenuItems = this.props.cultures.map((i)=>(
			<MenuItem key={i.id} value={i.id}>{i.name}</MenuItem>
		));
		const careerMenuItems = this.props.careers.map((i)=>(
			<MenuItem key={i.id} value={i.id}>{i.name}</MenuItem>
		));
		return (
			<div>
				<Box display="inline-block" mr="2rem">
					<InputLabel id="culture-label">Culture</InputLabel>
					<Select 	id="culture-select" labelId="culture-label"
								name="aCulture" displayEmpty
								className={classes.ccSelect}
								value={this.state.aCulture}
								onChange={(e):void => this.handleChange(e)}>
						<MenuItem value={0} disabled><em>Choose culture</em></MenuItem>
						{cultureMenuItems}
					</Select>
				</Box>
				<Box display="inline-block">
					<InputLabel id="career-label">Career</InputLabel>
					<Select 	id="career-select" labelId="career-label"
								name="aCareer"
								value={this.state.aCareer} 
								className={classes.ccSelect}
								onChange={(e):void => this.handleChange(e)}>
						<MenuItem value={0} disabled><em>Choose career</em></MenuItem>
						{careerMenuItems}
					</Select>
				</Box>
			</div>
		);
	}
}

//export default Selectors;
export default withStyles(selectorsStyles)(Selectors);