import React, { useState } from "react";
// import { makeStyles } from "@material-ui/core/styles";
import { SkillPack } from "./MsInterfaces";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import Selectors from "./Selectors";
import SkillList from "./SkillList";
import { cultureList, careerList } from "./MsData";

function App() {

	//Styles not currently used. Left here for example.
	// const useStyles = makeStyles((theme) => ({
	// 	root: {
	// 		flexGrow: 1
	// 	}
	// }));
	// const classes = useStyles();

	//Understand useState with TypeScript 
	//https://lucasbassetti.com/blog/using-usestate-hook-with-typescript/
	const [cultureSkills, setCultureSkills] = useState<string[]>([]);
	const [careerSkills, setCareerSkills] = useState<string[]>([]);
	const [unifiedSkills, setUnifiedSkills] = useState<string[]>([]);

	const selectorsChange = (cultureId: number, careerId: number) => {
		const culture: SkillPack | undefined = cultureList.find((el) => (el.id === cultureId));
		const career: SkillPack | undefined = careerList.find((el) => (el.id === careerId));
		if (culture) {
			setCultureSkills(culture.standard.concat(culture.professional).sort());
		}
		if (career) {
			setCareerSkills(career.standard.concat(career.professional).sort());
		}

		if (!culture || !career) return;
		const skills = culture.standard
			.concat(culture.professional)
			.concat(career.standard)
			.concat(career.professional).sort();
		const unified = Array.from(new Set(skills));

		setUnifiedSkills(unified);
	}

	return (
		<Container maxWidth="lg">
			<h1>M-Space Skills Generator</h1>
			<p>STR</p>
			<p>Choose your character&apos;s culture and career to obtain a skill list.</p>
			<Selectors cultures={cultureList} careers={careerList} onChange={selectorsChange} />
			<Grid container spacing={2}>
				<Grid item xs={12} md={4}>
					<p>
						<strong>Culture</strong>
						<br />
            Distribute 100 points among these skills
          </p>
					{cultureSkills.length > 0 &&
						<SkillList skills={cultureSkills} />
					}
				</Grid>
				<Grid item xs={12} md={4}>
					<p>
						<strong>Career</strong>
						<br />
            Distribute 100 points among these skills
          </p>
					{careerSkills.length > 0 &&
						<SkillList skills={careerSkills} />
					}
				</Grid>
				<Grid item xs={12} md={4}>
					<p>
						<strong>Unified Skills</strong><br />
            Warning: Combines *(any) skills
          </p>
					{unifiedSkills.length > 0 &&
						<SkillList skills={unifiedSkills} />
					}
				</Grid>
			</Grid>
		</Container>
	);
}

export default App;